import React from "react";
import ReactTable from "react-table";
import {Tooltip} from 'react-tippy'

export default class Test extends React.Component {
    constructor(props) {
        super(props)
        this.tippy = []
        this.bigboss = null
    }

    render() {
        return (
            <div ref={elm => this.bigboss = elm} onClick={(ev) => {
                console.log(ev.clientX, ev.clientY)
            }}>
                <Tooltip
                    ref={elm => this.tippy.push(elm)}
                    trigger="click"
                    interactive
                    size="big"
                    position="right"
                    theme="light"
                    html={
                        <button class="btn btn-green btn-3d" onClick={() => {
                            this.bigboss.click()
                        }}>LUL</button>
                    }>
                    <button class="btn btn-green btn-3d">
                        C9
                    </button>
                </Tooltip>
            </div>
        );
    }
}



